import React from "react";
import '../../style.css';

import {
    Button,
    Row,
    Col,
    Container
  } from 'reactstrap';



function Project3() {

    const handleVibrate = (time) => {
        if ("vibrate" in navigator) {
          navigator.vibrate(time);
        } else {
          alert("Vibration API not supported on this device.");
        }
      };

    const handleDefaultVibrate = () => {
        if ("vibrate" in navigator) {
            navigator.vibrate(200);
          } else {
            alert("Vibration API not supported on this device.");
          } 
        };
    
 
    return(
        <div>
            <h1>Project 3 - Haptics</h1>

            <h5><em>Tested with Android, Turn-off silent mode</em></h5>

            <Button onClick={handleDefaultVibrate}>200ms</Button>
            <Button onClick={handleDefaultVibrate}>200ms</Button>

            <Container>
                <Row>
                    <Col>
                        <Button onClick={handleVibrate(100)}>100ms</Button>
                    </Col>
                    <Col>
                        <Button onClick={handleDefaultVibrate}>200ms</Button>
                    </Col>
                    <Col>
                        <Button onClick={handleDefaultVibrate}>200ms</Button>
                    </Col>
                    <Col>
                        <Button onClick={handleVibrate(500)}>500ms</Button>
                    </Col>
                </Row>
            </Container>
            


        </div>
    );
}

export default Project3;